.hamburger__menu {

    .container {
        width: 100%;
        background-color: transparent;
        max-width: 1300px;
        margin: 0 auto;
        display: flex;
        justify-content: flex-end;
        position: fixed;
        width: 100%;
        left: 0;
        right: 0;
        top: 7%;
        background-color: transparent;
        z-index: 9;
        height: 0;
    }

    .burger {
        z-index: 20;
        cursor: pointer;
        width: 42px;
        height: 42px;
        border-radius: 50%;
        background-color: #DF524A;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        position: relative;
        margin-right: 10px;

        @media(min-width: 480px) {
            width: 50px;
            height: 50px;
        }

        @media(min-width: 600px) {
            margin-right: 30px;
        }

        div {
            width: 25px;

            @media(min-width: 480px) {
                width: 30px;
            }

            height: 4px;
            margin: 3px;
            background-color: white;
            border-radius: 5px;
            transition: all 0.3s ease;
        }

    }

    .toggle .line1 {
        transform: rotate(-45deg) translate(-7px, 7px);
    }

    .toggle .line2 {
        opacity: 0;
    }

    .toggle .line3 {
        transform: rotate(45deg) translate(-7px, -7px);
    }



    .menu {
        position: absolute;
        top: 0;
        right: -1%;
        z-index: 15;
        border-radius: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        box-shadow: 0 0 7px 0 white;
        width: 80%;
        max-width: 450px;
        background-color: #DF524A;
        opacity: 0;
        transform: translate(450px, -450px) scale(0);
        transition: all .5s ease-out;
        padding: 20px;


        @media(min-width: 600px) {
            margin-right: 30px;
        }

        &__name {
            text-transform: uppercase;
            font-size: 14px;
            color: white;
            font-weight: bold;
            margin-bottom: 10px;

            @media(min-width: 360px) {
                font-size: 16px;
            }

            @media(min-width: 430px) {
                margin-top: 10px;
                font-size: 18px;
            }

            @media(min-width: 900px) {
                font-size: 20px;
                margin: 20px;
            }
        }

        li {
            margin: 10px;
            box-shadow: 0 0 2px 0px white;
            opacity: 0;
            position: relative;

            a {
                cursor: pointer;
                display: flex;
                text-decoration: none;
                color: white;
                font-size: 18px;
                padding: 5px 10px;

                @media(min-width: 360px) {
                    font-size: 20px;
                    padding: 5px 12px;
                }

                @media(min-width: 430px) {
                    font-size: 24px;
                    padding: 5px 15px;
                }

                @media(min-width: 900px) {
                    font-size: 26px;
                    padding: 5px 20px;
                }

            }

            &:after {
                content: "";
                position: absolute;
                z-index: 1100;
                bottom: 0;
                left: 0;
                height: 2px;
                width: 0;
                background-color: white;
                transition: all .3s linear;

            }

            transition: all .3s linear;

            &:hover {
                &:after {
                    width: 100%;
                }

                box-shadow: 20px 0 2px 0 white;

            }
        }
    }

}

@keyframes navLinkFade {
    from {
        opacity: 0;
        transform: translateX(50px);
    }

    to {
        opacity: 1;
        transform: translateX(0px);
    }
}

@keyframes backNavLinkFade {
    from {
        opacity: 1;
        transform: translateX(0px);
    }

    to {
        opacity: 0;
        transform: translateX(50px);
    }
}

@keyframes menuAnimation {
    0% {
        transform: translate(450px, -450px) scale(0);
        opacity: 0;
    }

    90% {
        transform: translate(0, 0) scale(0.5);
        opacity: 1;

    }

    100% {
        transform: translate(0, 0) scale(1);
        opacity: 1;
    }
}

@keyframes backAnimation {
    0% {
        transform: translate(0, 0) scale(1);
        opacity: 1;


    }

    90% {
        transform: translate(0, 0) scale(0.5);
        opacity: 1;

    }

    100% {
        transform: translate(450px, -450px) scale(0);
        opacity: 0;
    }
}