.portfolio {

    .container {
        width: 100%;
        max-width: 1300px;
        margin: 0 auto;
    }

    .portfolio__title {
        text-align: center;
        color: white;
        text-transform: uppercase;
        letter-spacing: 3px;
        font-weight: bold;
        font-size: 28px;

        @media(min-width: 350px) {
            font-size: 32px;
        }

        @media(min-width: 450px) {
            font-size: 34px;
        }

        @media(min-width: 550px) {
            font-size: 36px;
        }

        @media(min-width: 650px) {
            font-size: 38px;
        }

        @media(min-width: 750px) {
            font-size: 40px;
        }
    }

    .portfolio__content {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
    }

    .portfolio__item {
        width: 80%;
        max-width: 600px;
        height: 250px;
        position: relative;
        border: 3px solid #DF524A;
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 10px;
        box-shadow: 0 0 4px 0 #DF524A;

        @media(min-width:400px) {
            height: 300px;
        }

        @media(min-width:500px) {
            height: 350px;
        }

        @media(min-width:700px) {
            height: 400px;
        }

        .portfolio__img {
            position: absolute;
            width: 100%;
            z-index: 2;
        }

        .portfolio__bg {
            position: absolute;
            width: 100%;
            height: 100%;
            z-index: 3;
            background-color: rgba(0, 0, 0, 0.65);
        }

        .portfolio__header {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            z-index: 4;
            width: 100%;
            text-align: center;
            color: white;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            h2 {
                font-size: 22px;
                text-shadow: 0 0 1px white;
                font-weight: bold;
                margin: 20px;

                @media(min-width: 350px) {
                    font-size: 24px;
                    letter-spacing: 1px;
                }

                @media(min-width: 450px) {
                    font-size: 26px;
                }

                @media(min-width: 550px) {
                    font-size: 32px;
                }
            }

            p {
                font-size: 16px;
                font-weight: bold;
                text-shadow: 0 0 9px black;
                color: #76b852;
                padding: 0 10px;

                @media(min-width: 450px) {
                    font-size: 18px;
                }

                @media(min-width: 550px) {
                    font-size: 20px;
                }
            }
        }

        .portfolio__buttons {
            display: flex;
            justify-content: center;
            align-items: center;

            button {
                background: none;
                background-color: white;
                margin: 15px;
                border: none;
                padding: 0;
                border-radius: 5px;
                transition: all .2s linear;
                position: relative;

                @media(min-width: 350px) {
                    margin: 20px;
                }

                a {
                    cursor: pointer;
                    text-decoration: none;
                    color: black;
                    display: flex;
                    border: 2px solid #DF524A;
                    margin: 0;
                    font-size: 14px;
                    border-radius: 5px;
                    padding: 10px 15px;

                    i {
                        margin-left: 15px;
                    }

                    @media(min-width: 350px) {
                        font-size: 16px;
                    }

                    @media(min-width: 420px) {
                        font-size: 18px;
                        padding: 10px 20px;
                    }

                    @media(min-width: 620px) {
                        padding: 10px 30px;
                    }
                }

                &:hover {
                    transform: scale(1.1);
                    background-color: #DF524A;

                    &::after {
                        left: 105%;
                    }

                    a {
                        color: white;
                    }
                }

                overflow: hidden;

                &::after {
                    content: "";
                    position: absolute;
                    top: 0;
                    bottom: 0;
                    left: -20%;
                    width: 15px;
                    transition: all .5s linear;
                    background-color: hsla(99, 42%, 52%, 0.4);
                    border-left: 3px solid #76b852;
                    border-right: 3px solid #76b852;
                }
            }
        }

    }
}