.contact {
    padding: 20px 0;
    background: #c31432;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right, #240b36, #c31432);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #240b36, #c31432);
    /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    box-shadow: 0 0 7px 1px black;
    margin-top: 20px;

    .contact__title {
        text-align: center;
        color: white;
        text-transform: uppercase;
        letter-spacing: 3px;
        font-weight: bold;
        font-size: 28px;
        padding: 20px 0;

        @media(min-width: 350px) {
            font-size: 32px;
        }

        @media(min-width: 450px) {
            font-size: 34px;
        }

        @media(min-width: 550px) {
            font-size: 36px;
        }

        @media(min-width: 650px) {
            font-size: 38px;
        }

        @media(min-width: 750px) {
            font-size: 40px;
        }
    }

    .container {
        width: 100%;
        max-width: 1300px;
        margin: 0 auto;
    }

    .contact__buttons {
        display: flex;
        justify-content: center;
        align-items: center;
        margin: 20px 10px;
        // box-shadow: 0 0 5px 1px #DF524A;
        background: #11998e;
        /* fallback for old browsers */
        background: -webkit-linear-gradient(to right, #38ef7d, #11998e);
        /* Chrome 10-25, Safari 5.1-6 */
        background: linear-gradient(to right, #38ef7d, #11998e);
        /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
        border-radius: 10px;
        max-width: 800px;

        @media(min-width:800px) {
            margin: 20px auto;

        }

        button {
            margin: 10px;
            padding: 0;
            background: none;
            cursor: pointer;
            border: none;

            a {
                display: flex;
                padding: 15px 15px;
                font-size: 26px;
                text-decoration: none;
                background-color: white;
                color: black;
                border-radius: 10px;
                transition: all linear .4s;
                font-weight: bold;

                &:hover {
                    transform: scale(1.2);
                    box-shadow: 0 0 7px 1px white;
                    border-radius: 25px;
                }

            }

        }

        .button__cv {

            a {
                font-size: 22px;
            }


        }
    }

    .contact__info {
        display: flex;
        align-items: center;
        flex-direction: column;

        p {
            padding: 10px 0;
            font-size: 18px;

            @media(min-width: 400px) {
                font-size: 20px;
            }

            @media(min-width: 500px) {

                font-size: 22px;
                letter-spacing: 0.5px;
            }

            i {
                color: #DF524A;
                margin-right: 10px;
            }

            color: white;
        }
    }

    .contact__form {
        background-color: white;
        padding: 20px 0;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        max-width: 800px;
        margin: 0 auto;
        color: black;

        h3 {
            text-align: center;
            font-size: 26px;
            background-color: #242526;
            color: white;
            font-weight: bold;
            padding: 10px 0;
            width: 100%;
            letter-spacing: 5px;
            margin-bottom: 30px;
        }

        @media(min-width: 800px) {
            border-radius: 10px;
        }

    }

    .input__item {
        display: flex;
        margin: 15px 0;
        text-align: end;


        input {
            height: 35px;
            width: 150px;
            font-size: 16px;
            padding: 10px;
            background-color: rgb(230, 230, 230);
            border: none;
            border-bottom: 2px solid #DF524A;
            border-left: 2px solid rgb(230, 230, 230);
            border-top: 2px solid rgb(230, 230, 230);
            border-right: 2px solid rgb(230, 230, 230);

            @media(min-width: 350px) {
                width: 180px;
            }

            @media(min-width: 400px) {
                width: 200px;
            }

            @media(min-width: 450px) {
                width: 250px;
            }

            @media(min-width: 550px) {
                height: 40px;
                width: 300px;
                font-size: 18px;

            }


            &:focus {
                outline: none;
                border-radius: 5px;
                border-top: 2px solid gray;
                border-left: 2px solid gray;
                border-right: 2px solid gray;
            }

        }

        .name__email {
            line-height: 35px;


            @media(min-width: 550px) {
                line-height: 40px;
            }
        }

        .lock {
            margin-left: 10px;
            font-size: 18px;
            color: rgb(199, 199, 199);

            @media(min-width: 550px) {
                font-size: 20px;
            }
        }

        .email__lock {
            line-height: 35px;


            @media(min-width: 550px) {
                line-height: 40px;
            }
        }

        textarea {
            font-family: sans-serif;
            font-size: 16px;
            height: 120px;
            width: 150px;
            min-width: 150px;
            max-width: 150px;
            min-height: 120px;
            max-height: 120px;
            padding: 10px;
            background-color: rgb(230, 230, 230);
            border: none;
            border-bottom: 2px solid #DF524A;
            border-left: 2px solid rgb(230, 230, 230);
            border-top: 2px solid rgb(230, 230, 230);
            border-right: 2px solid rgb(230, 230, 230);

            &:focus {
                outline: none;
                border-radius: 5px;
                border-top: 2px solid gray;
                border-left: 2px solid gray;
                border-right: 2px solid gray;
            }

            @media(min-width: 350px) {
                width: 180px;
                height: 180px;
                min-width: 180px;
                max-width: 180px;
                min-height: 180px;
                max-height: 250px;
            }

            @media(min-width: 400px) {
                width: 200px;
                height: 150px;
                min-width: 200px;
                max-width: 230px;
                min-height: 150px;
                max-height: 300px;
            }

            @media(min-width: 450px) {
                width: 250px;
                min-width: 250px;
                max-width: 280px;
            }


            @media(min-width: 550px) {
                font-size: 18px;
                height: 200px;
                width: 300px;
                min-width: 300px;
                max-width: 600px;
                min-height: 200px;
                max-height: 400px;
            }
        }

        h5 {
            font-size: 17px;
            width: 120px;
            margin-right: 10px;
            font-weight: bold;

            i {
                margin-right: 7px;
            }

            @media(min-width: 550px) {
                font-size: 20px;
                width: 150px;
                margin-right: 20px;
            }
        }
    }

    .btn {
        display: block;
        width: 100%;
        max-width: 300px;
        height: 50px;
        border-radius: 25px;
        outline: none;
        border: none;
        background-image: linear-gradient(to right, #32be8f, #38d39f, #32be8f);
        background-size: 200%;
        font-size: 1.2rem;
        color: #fff;
        font-family: 'Poppins', sans-serif;
        text-transform: uppercase;
        margin: 1rem 0;
        transition: .5s;
        opacity: 0.4;
        cursor: default;
    }

    .btn:hover {
        background-position: right;
    }

    .active {
        cursor: pointer;
        opacity: 1
    }

    .disabled {
        cursor: default;
        opacity: 0.4;
    }


}