@import "_reset.scss";
@import "_variables.scss";
@import "_menu.scss";
@import "_header.scss";
@import "_about.scss";
@import "_skills.scss";
@import "_portfolio.scss";
@import "_contact.scss";
@import "_footer.scss";

::selection {
    background-color: #DF524A;
    color: white;
}

::-webkit-scrollbar {
    background-color: #DF524A;
}

::-webkit-scrollbar-track {
    background-color: #090909;
}

::-webkit-scrollbar-thumb {
    background-color: #DF524A;
    border-radius: 2px;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #76b852;
}

.backTop {
    width: 100%;
    background-color: transparent;
    max-width: 1300px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;
    position: fixed;
    width: 100%;
    left: 0;
    right: 0;
    top: 90%;
    background-color: transparent;
    z-index: 9;
    height: 0;

    .active {
        transform: translateX(0);

    }

    button {
        transition: .2s linear all;

        cursor: pointer;
        width: 45px;
        height: 45px;
        border: none;
        background: none;
        background-color: rgba(0, 0, 0, 0.2);
        border: 2px solid #DF524A;
        border-radius: 50%;
        margin-right: 10px;
        color: white;
        transform: translateX(400px);
        position: relative;




        i {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            font-size: 30px;
            text-shadow: 0 30px 0 white;
            animation: animationToTop 1.5s infinite linear;
        }


        overflow: hidden;

        &:focus {
            outline: none;
        }

        @media(min-width: 900px) {
            width: 50px;
            height: 50px;

            &:hover {
                animation: vibrate-1 0.3s linear infinite both;
            }
        }
    }

}

@keyframes animationToTop {
    0% {
        transform: translate(-50%, -50%)
    }

    50% {
        transform: translate(-50%, -70%)
    }

    75% {
        transform: translate(-50%, -90%)
    }

    100% {
        transform: translate(-50%, -150%)
    }
}

@keyframes vibrate-1 {
    0% {
        -webkit-transform: translate(0);
        transform: translate(0);
    }

    20% {
        -webkit-transform: translate(-2px, 2px);
        transform: translate(-2px, 2px);
    }

    40% {
        -webkit-transform: translate(-2px, -2px);
        transform: translate(-2px, -2px);
    }

    60% {
        -webkit-transform: translate(2px, 2px);
        transform: translate(2px, 2px);
    }

    80% {
        -webkit-transform: translate(2px, -2px);
        transform: translate(2px, -2px);
    }

    100% {
        -webkit-transform: translate(0);
        transform: translate(0);
    }
}