.skills {

    .container {
        width: 100%;
        max-width: 1400px;
        margin: 0 auto;
        padding: 20px;
    }

    .skills__title {
        text-align: center;
        font-size: 24px;
        color: white;
        padding: 20px;
        font-weight: bold;
        letter-spacing: 3px;

        @media(min-width: 400px) {
            font-size: 28px
        }

        @media(min-width: 600px) {
            font-size: 32px
        }

        @media(min-width: 800px) {
            font-size: 40px
        }

    }

    .skills__description {
        text-align: center;
        font-size: 14px;
        line-height: 22px;
        letter-spacing: 3px;
        color: white;
        padding: 10px;

        @media(min-width: 400px) {
            font-size: 16px
        }

        @media(min-width: 600px) {
            font-size: 18px
        }

        @media(min-width: 800px) {
            font-size: 20px
        }
    }

    .skills__container {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: wrap;
        box-shadow: 0 0 7px 0 black;
        padding: 30px 0;

        .skill {
            margin: 20px;

            img {
                width: 80px;

                @media(min-width: 400px) {
                    width: 90px;
                }

                @media(min-width: 600px) {
                    width: 100px;
                }

                @media(min-width: 800px) {
                    width: 120px
                }

            }

            h4 {
                margin-bottom: 10px;
                font-size: 16px;
                color: white;

                @media(min-width: 400px) {
                    font-size: 18px;
                    margin-bottom: 12px;
                }

                @media(min-width: 600px) {
                    font-size: 20px;
                    margin-bottom: 15px;
                }

                @media(min-width: 800px) {
                    font-size: 22px;
                    margin-bottom: 18px;
                }
            }

        }
    }

    .skills__type {
        text-align: center;

        h3 {
            color: white;
            padding: 20px;
            font-size: 22px;
            font-weight: bold;

            @media(min-width: 400px) {
                font-size: 24px
            }

            @media(min-width: 600px) {
                font-size: 26px
            }

            @media(min-width: 800px) {
                font-size: 28px;
            }
        }
    }
}