.aboutMe {

    background-image: url("../img/bg.jpg");
    background-size: cover;
    background-position: top;
    // min-height: 100vh;

    &__bg {
        width: 100%;
        // min-height: 100vh;
        background-color: rgba(0, 0, 0, 0.7);

    }

    .container {
        width: 100%;
        max-width: 1200px;
        margin: 0 auto;
    }

    .aboutMe__content {
        margin-top: 30px;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding: 30px 0;

        @media(min-width: 1000px) {
            flex-direction: row;
        }
    }

    img {
        width: 90%;
        max-width: 450px;
        // margin-right: 20px;
        border-radius: 40px;
        margin: 0 auto;
        max-height: 450px;
        box-shadow: 10px -5px 5px 0px #DF524A;

        @media(min-width: 1000px) {
            margin-left: 20px;
            margin-right: 20px;
        }
    }

    h3 {
        text-align: center;
        padding: 30px 0;
        font-size: 35px;
        font-weight: bold;
        color: white;

        @media(min-width: 350px) {
            font-size: 40px;
        }

        @media(min-width: 450px) {
            font-size: 45px;
        }
    }

    p {
        font-size: 18px;
        text-align: justify;
        line-height: 22px;
        color: white;
        margin-top: 30px;
        margin-left: 15px;
        margin-right: 15px;
        padding-bottom: 20px;

        @media(min-width: 450px) {
            font-size: 20px;
            line-height: 24px;
            margin-left: 20px;
            margin-right: 20px;
        }

        @media(min-width: 850px) {
            margin-left: 22px;
            line-height: 26px;

        }

        a {
            color: #DF524A;
            cursor: pointer;
            font-weight: bold;
            text-decoration: underline;

        }
    }
}